                        <Link className="undertitle__link" to="/catalog/catalog-item">Моторное масло</Link>/
                        <Link className="undertitle__link" to="/catalog/catalog-item/for-commercial-vehicles">Для коммерческой техники</Link>/
                        <a className="undertitle__link">POWER THRUST PLUS CI-4</a>
